* {
	padding: 0px;
	margin: 0px;
	border: 0px;
	-webkit-tap-highlight-color: transparent;
	-webkit-text-size-adjust: none;
}
*,
*:before,
*:after {
	box-sizing: border-box;
}
:focus,
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}
html,
body {
	height: 100%;
	min-width: 320px;
}
body {
	color: var(--primary);
	font-family: var(--fontFamily);
	font-size: var(--fontSize);
	line-height: var(--lineHeight);
	letter-spacing: var(--letterSpacing);
}
input,
button,
textarea {
	font-family: inherit;
	font-size: inherit;
}
button {
	cursor: pointer;
	color: inherit;
	background-color: inherit;
}
a {
	color: inherit;
}
a:link,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
	object-fit: cover;
}
address {
	font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}

.link {
	display: block;
	padding: 10px 40px;
	border-radius: 7px;
	background-color: var(--secondary);
	color: #fff;
	border: 1px solid var(--lightGray);
	transition: all var(--anim);

	&:hover {
		background-color: var(--lightGray);
		color: var(--primary);
		border-color: var(--secondary);
	}
}


:root {
	
	--primary: #1b1b1b;
	--accent: #e50914;
	--secondary: #e95a5a;
	--gray: #767676;
	--lightGray: #e5e5e5;

	
	--fontFamily: "Open Sans", sans-serif;
	--fontSize: 16px;
	--lineHeight: 1.1;
	--letterSpacing: 0.04em;

	
	--anim: 0.3s ease;
}